.topNav{
    border: hidden;
    box-shadow: 0px 8px 8px rgba(2, 75, 140, 0.08);
    height: 80px;
    width: 375px;
    display: flex;
    align-items: center;
}

.topNav-header{
    justify-content: center;
    display: flex;
    float: left;
    width: 95%;
    font-size: 16px;
    font-weight: 600;
    line-height: 150%;
    color: #3F3F3F;
    font-weight: 600;
    font-size: 24px;
}

.topNav-back{
    font-size: 40px;
    cursor: pointer;
    align-items: center;
    color: #6A6B7C;
    width: 25px;
    height: 25px;
}

.topNav-back:hover{
    color: var(--color-primary);
}

